import React from "react"
import { FaChevronRight } from "react-icons/fa"

import { Container, CardButton, CardTitle, CardDescription } from "./styles"
import { IExpertiseCard } from "../../../../interfaces"

const SolutionCard = ({
	title,
	description,
	slug,
	thumbnailUrl,
}: IExpertiseCard) => {
	const handleClick = () => {
		window.open(slug, "_self")
	}

	return (
		<Container bgImage={thumbnailUrl} onClick={handleClick}>
			<div className="background-overlay"></div>

			<div className="content-wrapper">
				<CardTitle>{title}</CardTitle>
				<CardDescription>
					{description.map(item => {
						return <li key={item.id}>{item.item}</li>
					})}
				</CardDescription>
			</div>

			<CardButton>
				<FaChevronRight color="ffffff" size="20" />
			</CardButton>
		</Container>
	)
}

export default SolutionCard
