import React from "react"

import IntroductionContent from "../../utils/IntroductionContent"
import Banner from "../../utils/Banner"
import { ISolutionCard } from "../../../interfaces"

import SolutionCard from "./SolutionCard"
import BookCall from "../../utils/BookCall"

interface IProps {
	expertise: Array<ISolutionCard>
}

export default function Expertise(props: IProps) {
	const expertise = props.expertise

	return (
		<div>
			<Banner titlePage="Áreas de Atuação" />
			<IntroductionContent
				title="Como a Kivalita pode te ajudar?"
				subtitle="ÁREAS DE ATUAÇÃO"
				content="A Kivalita Consulting tem as mais diversas soluções para o seu negócio. Encontre abaixo sobre os nossos serviços e clique para saber mais."
			/>

			<div className="container">
				<div className="row">
					{expertise.map((item, idx) => (
						<div
							className={"col-md-12 col-lg-6 col-xl-" + item.colSize}
							key={idx}
						>
							<SolutionCard {...item} />
						</div>
					))}
				</div>
				<BookCall />
			</div>
		</div>
	)
}
