import React from "react"

import Layout from "../components/Layout"

import Expertise from "../components/pages/AreasExpertise"

import expertise from "../data/expertise.json"
import SEO from "../components/utils/SEO"
import { translate } from "../components/utils/translate"

const AreasExpertise = () => {
	const breadCrumbList: IBreadCrumbOption[] = [
		{
			url: "/",
			name: "Home",
			isActive: true,
		},
		{
			url: "areas-expertise",
			name: translate("expertise.breadCrumb.name"),
			isActive: false,
		},
	]

	return (
		<Layout breadCrumbList={breadCrumbList}>
			<SEO></SEO>
			<Expertise expertise={expertise} />
		</Layout>
	)
}

export default AreasExpertise
